body {
    background-image: url("./images/background11.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.cell {
    background-size: contain;
    border-style: solid;
    border-color: gray;
    border-width: 0;
    box-shadow: inset 0 0 10px 0 rgba(179,177,179,1);
}

.drag-area {
    border-radius: 0.25rem;
    border-color: #e2d85c;
    border-style: dashed;
}

.console {
    background-color: #545454;
    color: white;
    border-color: black;
    border-style: solid;
    border-width: 2px;
    width: 25%;
    min-height: 200px;
}